<template>
  <v-app-bar app dense fixed color="btnPrimary">
    <!-- Menu -->
    <v-tooltip bottom>
      <template v-slot:activator="{ on, attrs }">
        <v-app-bar-nav-icon
          style="color: #fff"
          @click="setOpenPanelServiceList(true)"
          v-bind="attrs"
          v-on="on"
        ></v-app-bar-nav-icon>
      </template>
      <span>{{ $t("message.44") }}</span>
    </v-tooltip>
    <v-toolbar-title class="white--text">{{ brand }}</v-toolbar-title>
    <v-spacer></v-spacer>
    <!-- Light or dark theme button -->
    <v-tooltip bottom>
      <template v-slot:activator="{ on, attrs }">
        <v-btn icon v-bind="attrs" v-on="on" @click="changeTheme()">
          <v-icon color="white">mdi-brightness-4</v-icon>
        </v-btn>
      </template>
      <span>{{ $t("message.1") }}</span>
    </v-tooltip>
    <!-- The choice of color theme -->
    <v-menu left bottom>
      <template v-slot:activator="{ on: menu, attrs }">
        <v-tooltip bottom>
          <template v-slot:activator="{ on: tooltip }">
            <v-btn
              icon
              class="mx-2"
              v-bind="attrs"
              v-on="{ ...tooltip, ...menu }"
            >
              <v-icon color="white">mdi-palette</v-icon>
            </v-btn>
          </template>
          <span>{{ $t("message.36") }}</span>
        </v-tooltip>
      </template>
      <v-list dense>
        <v-list-item-group v-model="updateSelectedTheme" color="primaryTwo">
          <v-list-item
            v-for="(theme, idx) in themeList"
            :key="idx"
            @click="
              changeColorTheme({
                color: theme.color,
                themes: $vuetify.theme.themes,
              })
            "
          >
            <v-list-item-content>
              <v-list-item-title>{{ theme.name }}</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </v-list-item-group>
      </v-list>
    </v-menu>
    <!-- Choice localization of application -->
    <v-menu left bottom>
      <template v-slot:activator="{ on, attrs }">
        <v-btn text color="white" class="mx-2" v-bind="attrs" v-on="on">
          {{ currentLang }}
        </v-btn>
      </template>
      <v-list dense>
        <v-list-item-group v-model="updateSelectedLang" color="primaryTwo">
          <v-list-item
            v-for="(lang, idx) in langList"
            :key="idx"
            @click="changeLocalization(lang)"
          >
            <v-list-item-content>
              <v-list-item-title>{{ lang.name }}</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </v-list-item-group>
      </v-list>
    </v-menu>
    <!-- Logout -->
    <v-tooltip bottom>
      <template v-slot:activator="{ on, attrs }">
        <v-btn icon v-bind="attrs" v-on="on" @click="logout()">
          <v-icon color="white">mdi-logout</v-icon>
        </v-btn>
      </template>
      <span>{{ $t("message.2") }}</span>
    </v-tooltip>
  </v-app-bar>
</template>

<script>
import { mapState, mapMutations, mapActions } from "vuex";

export default {
  name: "AppBar",

  computed: {
    ...mapState(["brand", "openPanelServiceList", "colorThemes"]),
    ...mapState("appBar", [
      "selectedTheme",
      "selectedLang",
      "currentLang",
      "langList",
      "themeList",
    ]),
    updateSelectedTheme: {
      get: function () {
        return this.selectedTheme;
      },
      set: function (num) {
        this.setSelectedTheme(num);
      },
    },
    updateSelectedLang: {
      get: function () {
        return this.selectedLang;
      },
      set: function (langCode) {
        this.setSelectedLang(langCode);
      },
    },
  },

  methods: {
    ...mapMutations(["setIsAuthenticated", "setOpenPanelServiceList"]),
    ...mapMutations("popUpMsg", ["setShowMsg"]),
    ...mapMutations("appBar", [
      "setSelectedTheme",
      "setSelectedLang",
      "setCurrentLang",
    ]),
    ...mapActions("appBar", ["changeColorTheme"]),
    ...mapActions("pageLockout", ["runShowOverlayPageLockout"]),
    ...mapActions("popUpMsg", ["runShowMsg"]),
    logout() {
      this.setShowMsg(false);
      this.axios
        .post("/admin/logout", {})
        .then(() => {
          this.setIsAuthenticated(false);
        })
        .catch((error) => {
          console.log(error);
        });
    },
    changeTheme() {
      this.$vuetify.theme.dark = !this.$vuetify.theme.dark;
    },
    changeLocalization(lang) {
      this.setShowMsg(false);
      this.setCurrentLang(lang.name);
      this.runShowOverlayPageLockout(true);
      this.axios
        .get("/admin/change-current-lang", { lang_code: lang.code })
        .then((response) => {
          const data = response.data;
          if (!data.is_authenticated) {
            this.setIsAuthenticated(false);
          } else {
            location.reload();
          }
        })
        .catch((error) => {
          console.log(error);
          this.runShowMsg({ text: error, status: "error" });
        });
    },
  },
};
</script>
